/* --- PAGE LAYOUT --- */
.threed-page-container {
  min-height: 100vh;
  padding: 2rem;
  padding-top: 40px;
  position: relative;
  z-index: 10;
  max-width: 1400px;
  margin: 0 auto;
}

/* --- NAVIGATION --- */
.threed-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  
  /* Desktop: Keep it simple */
  position: static; 
  width: 100%;
}

.threed-navbar h2 {
  color: var(--primary-color, #ffc107);
  margin: 0;
  font-size: 1.5rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.back-button {
  color: #fff;
  text-decoration: none;
  font-weight: 600;
  padding: 10px 25px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 30px;
  transition: all 0.3s ease;
  background: rgba(0, 0, 0, 0.5);
  white-space: nowrap; /* Prevents text wrapping */
}

.back-button:hover {
  background: var(--primary-color, #ffc107);
  color: #000;
  border-color: var(--primary-color, #ffc107);
}

/* --- GRID LAYOUT --- */
.threed-grid {
  display: grid;
  /* Auto-fit with a minimum of 300px, or 100% if screen is smaller */
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  padding-bottom: 4rem;
}

/* --- CARD STYLING --- */
.threed-card {
  background: rgba(20, 20, 20, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  overflow: hidden;
  transition: transform 0.3s ease, border-color 0.3s ease;
  backdrop-filter: blur(10px);
  display: flex;
  flex-direction: column;
}

.threed-card:hover {
  transform: translateY(-5px);
  border-color: var(--primary-color, #ffc107);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
}

/* --- MODEL CONTAINER --- */
.model-display {
  width: 100%;
  height: 400px; /* Default desktop height */
  background: radial-gradient(circle at center, #2a2a2a 0%, #0a0a0a 100%);
  position: relative;
}

/* --- TEXT DETAILS --- */
.project-details {
  padding: 1.5rem;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
  background: rgba(0, 0, 0, 0.2);
}

.project-details h3 {
  margin: 0 0 0.5rem 0;
  color: #fff;
  font-size: 1.2rem;
}

.project-details p {
  margin: 0;
  color: #888;
  font-size: 0.9rem;
}

/* --- CURSOR OVERRIDES (Hand Cursor) --- */
.model-wrapper, model-viewer {
    cursor: grab !important;
}
.model-wrapper:active, model-viewer:active {
    cursor: grabbing !important;
}

/* =========================================
   MOBILE RESPONSIVENESS
   ========================================= */

@media (max-width: 768px) {
  .threed-page-container {
    padding: 1.5rem 1rem; /* Less padding on sides */
  }

  /* Stack Navbar Vertically */
  .threed-navbar {
    flex-direction: column-reverse; /* Puts "Back" button on top, Title below */
    gap: 1.5rem;
    align-items: center;
    text-align: center;
    margin-bottom: 2rem;
  }

  .threed-navbar h2 {
    font-size: 1.8rem;
  }

  /* Make grid single column on tablets/mobile */
  .threed-grid {
    grid-template-columns: 1fr; 
    gap: 2.5rem;
  }

  /* Slightly smaller model view */
  .model-display {
    height: 350px;
  }
}

@media (max-width: 480px) {
  /* Small Phones */
  .threed-page-container {
    padding: 1rem;
  }

  .model-display {
    height: 300px; /* Smaller height for small screens */
  }

  .project-details {
    padding: 1.2rem;
  }
}