/* Global Styles */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&family=Poppins:wght@300;400;500;600;700&display=swap');

:root {
  --primary-color: #ffc107;
  --secondary-color: #ff3d00;
  --bg-color: #111111;
  --dark-bg: #0a0a0a;
  --light-bg: #1e1e1e;
  --text-color: #ffffff;
  --text-secondary: #aaaaaa;
  --text-tertiary: #666666;
  --transition: all 0.3s ease;
  --shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  scrollbar-width: thin;
  scrollbar-color: var(--primary-color) var(--bg-color);
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: var(--bg-color);
  color: var(--text-color);
  overflow-x: hidden;
  line-height: 1.6;
  position: relative;
  cursor: none;
}

body, html {
  cursor: none;
}

/* Custom Scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--bg-color);
}

::-webkit-scrollbar-thumb {
  background: var(--primary-color);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--secondary-color);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 1rem;
}

h1 {
  font-size: 5rem;
  letter-spacing: -2px;
}

h2 {
  font-size: 3rem;
  letter-spacing: -1px;
}

h3 {
  font-size: 1.8rem;
  letter-spacing: -0.5px;
}

p {
  margin-bottom: 1.5rem;
  color: var(--text-secondary);
}

a {
  text-decoration: none;
  color: var(--text-color);
  transition: var(--transition);
}

a:hover {
  color: var(--primary-color);
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.btn {
  display: inline-block;
  padding: 12px 28px;
  font-weight: 500;
  border-radius: 30px;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: var(--transition);
  font-size: 0.9rem;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  transition: width 0.3s ease;
  z-index: -1;
}

.btn:hover::before {
  width: 100%;
}

.primary-btn {
  background-color: var(--primary-color);
  color: var(--bg-color);
  border: none;
}

.primary-btn:hover {
  color: var(--bg-color);
  box-shadow: 0 5px 15px rgba(255, 193, 7, 0.3);
}

.secondary-btn {
  background-color: transparent;
  color: var(--text-color);
  border: 2px solid var(--primary-color);
}

.secondary-btn:hover {
  border-color: var(--secondary-color);
}

.container {
  width: 100%;
  min-height: 100vh;
}

/* Loader */
.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--bg-color);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  transition: opacity 0.5s ease, visibility 0.5s ease;
}

.loader-content {
  text-align: center;
}

.loader-circle {
  width: 60px;
  height: 60px;
  border: 4px solid var(--text-tertiary);
  border-top: 4px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px;
}

.loader h2 {
  font-size: 1.2rem;
  letter-spacing: 5px;
  color: var(--primary-color);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.hidden {
  visibility: hidden;
  opacity: 0;
}
/* Custom Cursor */
.rocket-cursor {
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: 10000;
  transform: translate(-50%, -50%);
  transition: transform 0.1s ease-out;
}

.rocket-cursor svg {
  width: 24px;
  height: 24px;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
  transform: rotate(-45deg);
  transition: transform 0.2s ease, filter 0.2s ease;
}

.rocket-cursor.hover {
  transform: translate(-50%, -50%) scale(1.3);
}

.rocket-cursor.hover svg {
  transform: rotate(-45deg) scale(1.1);
  filter: drop-shadow(0 4px 8px rgba(255, 193, 7, 0.4));
}

.rocket-cursor.click svg {
  transform: rotate(-45deg) scale(0.9);
}

/* Hide default cursor when rocket cursor is active */
* {
  cursor: none !important;
}

/* Add smooth color transition for SVG elements */
.rocket-cursor svg path,
.rocket-cursor svg circle {
  transition: fill 0.2s ease;
}

/* --- NEW STYLES FOR THE TRAIL --- */

.rocket-trail {
  position: fixed;
  /* Must be less than the rocket's z-index (10000) */
  z-index: 9999;
  width: 10px;
  height: 10px;
  background: white;
  border-radius: 50%;
  pointer-events: none;
  /* Center the particle on the cursor offset */
  transform: translate(-50%, -50%);
  /* Run the animation */
  animation: fadeOut 3s forwards;
}

@keyframes fadeOut {
  from {
    opacity: 0.6;
    transform: translate(-50%, -50%) scale(1);
    filter: blur(3px);
  }
  to {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.3);
    filter: blur(10px);
  }
}
/* Navigation */
nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 30px 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
  transition: var(--transition);
}

nav.scrolled {
  background-color: rgba(10, 10, 10, 0.95);
  padding: 15px 50px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
}

.logo a {
  font-family: 'Montserrat', sans-serif;
  font-weight: 800;
  font-size: 2rem;
  color: var(--text-color);
}

.logo span {
  color: var(--primary-color);
}

.nav-toggle {
  display: none;
  flex-direction: column;
  cursor: pointer;
}

.nav-toggle span {
  width: 30px;
  height: 3px;
  background-color: var(--text-color);
  margin: 4px 0;
  transition: var(--transition);
}

.nav-links {
  display: flex;
  list-style: none;
}

.nav-links li {
  margin: 0 15px;
}

.nav-links a {
  font-weight: 500;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 5px 10px;
  position: relative;
}

.nav-links a::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  background-color: var(--primary-color);
  transition: width 0.3s ease, left 0.3s ease;
}

.nav-links a:hover::after,
.nav-links a.active::after {
  width: 100%;
  left: 0;
}

.social-icons {
  display: flex;
}

.social-icons a {
  margin-left: 15px;
  font-size: 1.2rem;
  transition: var(--transition);
}

.social-icons a:hover {
  color: var(--primary-color);
  transform: translateY(-3px);
}

/* Section Styling */
.section {
  padding: 120px 50px;
  position: relative;
  overflow: hidden;
}

.section-header {
  text-align: center;
  margin-bottom: 80px;
  position: relative;
}

.section-header h2 {
  position: relative;
  display: inline-block;
}

.accent-line {
  width: 60px;
  height: 4px;
  background-color: var(--primary-color);
  margin: 10px auto 0;
}

/* Home Section */
#home {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-left: 10%;
  position: relative;
}

.canvas-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
}

.home-content {
  max-width: 700px;
  z-index: 1;
}

.text-reveal h1 {
  margin-bottom: 0;
  line-height: 1;
}

.subtitle {
  margin: 30px 0;
}

.subtitle p {
  font-size: 1.2rem;
  letter-spacing: 2px;
  color: var(--primary-color);
  font-weight: 500;
}

.home-description p {
  font-size: 1.3rem;
  margin-bottom: 40px;
}

.cta-buttons {
  display: flex;
  gap: 20px;
}

.scroll-indicator {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0.7;
  transition: var(--transition);
}

.scroll-indicator p {
  font-size: 0.8rem;
  letter-spacing: 3px;
  margin-bottom: 10px;
}

.scroll-arrow {
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-15px);
  }
  60% {
    transform: translateY(-7px);
  }
}

/* About Section */
.about-container {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  grid-gap: 50px;
  align-items: center;
}

.image-container {
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 80%;
  height: auto;
  margin: 0 auto;
}

.image-container img {
  width: 80%;
  height: auto;
  display: block;
  transition: transform 0.5s ease;
  object-fit: cover;
  z-index: 1;
}

.image-container:hover img {
  transform: scale(1.05);
}

.accent-circle {
  position: absolute;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  background-color: var(--primary-color);
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  opacity: 0.8;
}

.about-content h3 {
  color: var(--primary-color);
  margin-top: 30px;
  margin-bottom: 15px;
}

.about-content h3:first-child {
  margin-top: 0;
}

.about-cta {
  margin-top: 40px;
  display: flex;
  gap: 20px;
}

/* Projects Section */
/* Projects Section Layout */
.projects-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .projects-container {
    grid-template-columns: 1fr;
  }
}

/* Project Card Styles */
.project-item {
  display: flex;
  flex-direction: column;
  opacity: 0;
  transform: translateY(50px);
  border-radius: 10px;
  overflow: hidden;
  transition: opacity 0.6s ease-out, transform 0.6s ease-out, box-shadow 0.3s ease;
  background-color: var(--light-bg); /* Ensure card has a background */
}

.project-item.visible {
  opacity: 1;
  transform: translateY(0);
}

.project-item:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.project-image {
  position: relative;
  border-radius: 10px 10px 0 0;
  overflow: hidden;
  box-shadow: var(--shadow);
}

.project-image img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  transition: transform 0.5s ease;
  display: block;
}

.project-item:hover .project-image img {
  transform: scale(1.05);
}

/* Overlay Styles */
.project-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.project-image:hover .project-overlay {
  opacity: 1;
}

.overlay-content h4 {
  color: var(--primary-color);
  font-size: 1.5rem;
  letter-spacing: 2px;
  border: 2px solid var(--primary-color);
  padding: 10px 20px;
  cursor: pointer;
}

.project-info {
  padding: 20px;
}

.project-info h3 {
  margin-bottom: 15px;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 20px;
}

.project-tags span {
  background-color: var(--light-bg); /* Or use a specific color variable */
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
  padding: 5px 15px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
}

.section-cta {
  text-align: center;
  margin-top: 80px;
}

/* --- IMPORTANT: ANIMATION CODE --- */

/* 1. Define the movement (Start lower and invisible, move up and become visible) */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* 2. Apply the animation class */
.project-item.fade-in {
  opacity: 0; /* Hide initially */
  /* The animation duration is 0.6s. 'forwards' ensures it stays visible at the end. */
  animation: fadeInUp 0.6s ease-out forwards;
}

/* Skills Section */
.skills-container {
  display: flex;
  flex-direction: column;
  gap: 80px;
}

.skill-category h3 {
  text-align: center;
  margin-bottom: 50px;
  color: var(--primary-color);
  letter-spacing: 2px;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px;
}

.skill-item {
  background-color: var(--light-bg);
  padding: 30px;
  border-radius: 10px;
  text-align: center;
  transition: var(--transition);
  position: relative;
  overflow: hidden;
}

.skill-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: var(--primary-color);
  transform: translateX(-100%);
  transition: transform 0.5s ease;
}

.skill-item:hover::before {
  transform: translateX(0);
}

.skill-item:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
}

.skill-icon {
  font-size: 2.5rem;
  color: var(--primary-color);
  margin-bottom: 20px;
}

.skill-bar {
  width: 100%;
  height: 6px;
  background-color: var(--dark-bg);
  border-radius: 3px;
  margin-top: 15px;
  overflow: hidden;
}

.skill-progress {
  height: 100%;
  background-color: var(--primary-color);
  width: 0; 
  border-radius: 3px;
  transition: width 1.5s ease-out;
}

/* Contact Section */
.contact-container {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  grid-gap: 50px;
}

.contact-info h3 {
  margin-bottom: 20px;
  color: var(--primary-color);
}

.contact-details {
  margin: 40px 0;
}

.contact-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.contact-icon {
  width: 50px;
  height: 50px;
  background-color: var(--light-bg);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  color: var(--primary-color);
  font-size: 1.2rem;
}

.contact-social {
  display: flex;
  gap: 15px;
}

.contact-social a {
  width: 50px;
  height: 50px;
  background-color: var(--light-bg);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  transition: var(--transition);
}

.contact-social a:hover {
  background-color: var(--primary-color);
  color: var(--dark-bg);
  transform: translateY(-5px);
}

.contact-form {
  background-color: var(--light-bg);
  padding: 40px;
  border-radius: 10px;
}

.form-group {
  margin-bottom: 25px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 12px 15px;
  background-color: var(--dark-bg);
  border: 1px solid var(--text-tertiary);
  border-radius: 5px;
  color: var(--text-color);
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  transition: var(--transition);
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(255, 193, 7, 0.2);
}

/* Footer */
footer {
  background-color: var(--dark-bg);
  padding: 80px 50px 30px;
}

.footer-content {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 30px;
  margin-bottom: 50px;
}

.footer-logo h2 {
  font-size: 2.5rem;
  margin-bottom: 15px;
}

.footer-logo span {
  color: var(--primary-color);
}

.footer-links {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.footer-links a {
  position: relative;
  padding-left: 15px;
}

.footer-links a::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 6px;
  height: 6px;
  background-color: var(--primary-color);
  border-radius: 50%;
}

.footer-newsletter h3 {
  margin-bottom: 20px;
  font-size: 1.5rem;
}

.newsletter-form {
  display: flex;
}

.newsletter-form input {
  flex: 1;
  padding: 12px 15px;
  background-color: var(--light-bg);
  border: 1px solid var(--text-tertiary);
  border-radius: 5px 0 0 5px;
  color: var(--text-color);
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
}

.newsletter-form button {
  padding: 0 20px;
  background-color: var(--primary-color);
  color: var(--dark-bg);
  border: none;
  border-radius: 0 5px 5px 0;
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
}

.newsletter-form button:hover {
  background-color: var(--secondary-color);
}

/* Animation Classes */
.fade-in {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.8s ease, transform 0.8s ease;
}

.fade-in.active {
  opacity: 1;
  transform: translateY(0);
}

.scale-in {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 0.8s ease, transform 0.8s ease;
}

.scale-in.active {
  opacity: 1;
  transform: scale(1);
}

/* Media Queries */
@media screen and (max-width: 1200px) {
  h1 {
    font-size: 4rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  .skills-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 992px) {
  .section {
    padding: 100px 30px;
  }
  nav {
    padding: 20px 30px;
  }
  .projects-container {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  .project-item.reverse .project-image {
    order: 0;
  }
  .project-item.reverse .project-info {
    order: 0;
  }
  .about-container,
  .project-item,
  .project-item.reverse,
  .contact-container {
    grid-template-columns: 1fr;
  }
  .project-item.reverse .project-image,
  .project-item.reverse .project-info {
    order: unset;
  }
  .footer-content {
    grid-template-columns: 1fr;
    gap: 50px;
  }
}

@media screen and (max-width: 768px) {
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 2rem;
  }
  .nav-toggle {
    display: flex;
    z-index: 200;
  }
  .nav-links {
    position: fixed;
    top: 0;
    right: -100%;
    width: 70%;
    height: 100vh;
    background-color: var(--dark-bg);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    transition: right 0.5s ease;
    z-index: 100;
  }
  .nav-links.active {
    right: 0;
  }
  .social-icons {
    display: none;
  }
  .skills-grid {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 480px) {
  h1 {
    font-size: 2.5rem;
  }
  .section {
    padding: 80px 20px;
  }
  .cta-buttons {
    flex-direction: column;
    gap: 15px;
  }
  .btn {
    width: 100%;
    text-align: center;
  }
  .newsletter-form {
    flex-direction: column;
  }
  .newsletter-form input {
    border-radius: 5px;
    margin-bottom: 10px;
  }
  .newsletter-form button {
    border-radius: 5px;
    padding: 12px;
  }
}
